import React, {useState, useEffect, useContext, Suspense, lazy} from "react";
import "./Project.scss";
import Button from "../../components/button/Button";
import {openSource, socialMediaLinks} from "../../portfolio";
import StyleContext from "../../contexts/StyleContext";
import Loading from "../../containers/loading/Loading";
export default function Projects() {
  const GithubRepoCard = lazy(() =>
    import("../../components/githubRepoCard/GithubRepoCard")
  );
  const FailedLoading = () => null;
  const renderLoader = () => <Loading />;
  const [repo, setrepo] = useState([]);
  // todo: remove useContex because is not supported
  const {isDark} = useContext(StyleContext);

  useEffect(() => {
    const getRepoData = () => {
      fetch(
        "https://mpardhi11.github.io/mohit-s-portfolio-website/profile.json"
      )
        .then(result => {
          if (result.ok) {
            const response = {
              data: {
                user: {
                  name: "Mohit Pardhi",
                  bio: "",
                  avatarUrl:
                    "https://avatars.githubusercontent.com/u/34490195?u=420b85f997d4d3c5f06688794a3e15798dd9180e&v=4",
                  location: "India",
                  pinnedItems: {
                    totalCount: 5,
                    edges: [
                      {
                        node: {
                          name: "dice-game",
                          description: null,
                          forkCount: 0,
                          stargazers: {totalCount: 0},
                          url: "https://github.com/mpardhi11/dice-game",
                          id: "R_kgDOLGn6Gg",
                          diskUsage: 1405,
                          primaryLanguage: {
                            name: "JavaScript",
                            color: "#f1e05a"
                          }
                        }
                      },
                      {
                        node: {
                          name: "e-book-store",
                          description: null,
                          forkCount: 0,
                          stargazers: {totalCount: 0},
                          url: "https://github.com/mpardhi11/e-book-store",
                          id: "R_kgDOLEeYyg",
                          diskUsage: 27601,
                          primaryLanguage: {
                            name: "JavaScript",
                            color: "#f1e05a"
                          }
                        }
                      },
                      {
                        node: {
                          name: "food-center",
                          description: null,
                          forkCount: 0,
                          stargazers: {totalCount: 0},
                          url: "https://github.com/mpardhi11/food-center",
                          id: "R_kgDOLHQagw",
                          diskUsage: 7073,
                          primaryLanguage: {
                            name: "JavaScript",
                            color: "#f1e05a"
                          }
                        }
                      },
                      {
                        node: {
                          name: "contact-us-page",
                          description: null,
                          forkCount: 0,
                          stargazers: {totalCount: 0},
                          url: "https://github.com/mpardhi11/contact-us-page",
                          id: "R_kgDOLFpFUg",
                          diskUsage: 942,
                          primaryLanguage: {
                            name: "JavaScript",
                            color: "#f1e05a"
                          }
                        }
                      },
                      {
                        node: {
                          name: "coffee-grid",
                          description: "Coffee shop using React and M-UI",
                          forkCount: 0,
                          stargazers: {totalCount: 0},
                          url: "https://github.com/mpardhi11/coffee-grid",
                          id: "R_kgDOJ9L32A",
                          diskUsage: 1816,
                          primaryLanguage: {
                            name: "JavaScript",
                            color: "#f1e05a"
                          }
                        }
                      }
                    ]
                  }
                }
              }
            };
            return response;
          }
          throw result;
        })
        .then(response => {
          setrepoFunction(response.data.user.pinnedItems.edges);
        })
        .catch(function (error) {
          console.error(
            `${error} (because of this error, nothing is shown in place of Projects section. Also check if Projects section has been configured)`
          );
          setrepoFunction("Error");
        });
    };
    getRepoData();
  }, []);

  function setrepoFunction(array) {
    setrepo(array);
  }
  if (
    !(typeof repo === "string" || repo instanceof String) &&
    openSource.display
  ) {
    return (
      <Suspense fallback={renderLoader()}>
        <div className="main" id="opensource">
          <h1 className="project-title">Open Source Projects</h1>
          <div className="repo-cards-div-main">
            {repo.map((v, i) => {
              if (!v) {
                console.error(
                  `Github Object for repository number : ${i} is undefined`
                );
              }
              return (
                <GithubRepoCard repo={v} key={v.node.id} isDark={isDark} />
              );
            })}
          </div>
          <Button
            text={"More Projects"}
            className="project-button"
            href={socialMediaLinks.github}
            newTab={true}
          />
        </div>
      </Suspense>
    );
  } else {
    return <FailedLoading />;
  }
}
