import React, {useState, useEffect, lazy, Suspense} from "react";
import {openSource} from "../../portfolio";
import Contact from "../contact/Contact";
import Loading from "../loading/Loading";

const renderLoader = () => <Loading />;
const GithubProfileCard = lazy(() =>
  import("../../components/githubProfileCard/GithubProfileCard")
);
export default function Profile() {
  const [prof, setrepo] = useState([]);
  function setProfileFunction(array) {
    setrepo(array);
  }

  useEffect(() => {
    if (openSource.showGithubProfile === true) {
      const getProfileData = () => {
        fetch(
          "https://mpardhi11.github.io/mohit-s-portfolio-website/profile.json"
        )
          .then(result => {
            if (result.ok) {
              // const response = result.json();
              const response = {
                data: {
                  user: {
                    name: "Mohit Pardhi",
                    bio: ` Hi there! I'm Mohit, a passionate web developer with expertise in Node.js, JavaScript, TypeScript, and various databases. I love building innovative solutions and contributing to open-source projects. When I'm not coding, you can find me exploring new technologies or enjoying outdoor activities. Let's connect and collaborate on exciting projects!`,
                    avatarUrl:
                      "https://avatars.githubusercontent.com/u/34490195?u=420b85f997d4d3c5f06688794a3e15798dd9180e&v=4",
                    location: "India",
                    pinnedItems: {
                      totalCount: 1,
                      edges: [
                        {
                          node: {
                            name: "dice-game",
                            description: null,
                            forkCount: 0,
                            stargazers: {totalCount: 0},
                            url: "https://github.com/mpardhi11/dice-game",
                            id: "R_kgDOLGn6Gg",
                            diskUsage: 1405,
                            primaryLanguage: {
                              name: "JavaScript",
                              color: "#f1e05a"
                            }
                          }
                        }
                      ]
                    }
                  }
                }
              };
              return response;
            }
          })
          .then(response => {
            setProfileFunction(response.data.user);
          })
          .catch(function (error) {
            console.error(
              `${error} (because of this error GitHub contact section could not be displayed. Contact section has reverted to default)`
            );
            setProfileFunction("Error");
            openSource.showGithubProfile = "false";
          });
      };
      getProfileData();
    }
  }, []);
  if (
    openSource.display &&
    openSource.showGithubProfile === true &&
    !(typeof prof === "string" || prof instanceof String)
  ) {
    return (
      <Suspense fallback={renderLoader()}>
        <GithubProfileCard prof={prof} key={prof.id} />
      </Suspense>
    );
  } else {
    return <Contact />;
  }
}
